<template>
  <div class="res_wrapper">
    <em class="iconfont">&#xe771;</em>
    <div class="ok">{{ok}}</div>
    <div class="p">{{p}}</div>
    <div @click="close()" class="btn">关闭</div>
  </div>
</template>

<script>

export default {
  name: 'tousuRes',
  components: {
    
  },
  data () {
    return {
      ok: decodeURIComponent("%E6%8A%95%E8%AF%89%E5%B7%B2%E6%8F%90%E4%BA%A4"),
      p: decodeURIComponent("%E5%BE%AE%E4%BF%A1%E5%9B%A2%E9%98%9F%E4%BC%9A%E5%B0%BD%E5%BF%AB%E6%A0%B8%E5%AE%9E%EF%BC%8C%E5%B9%B6%E9%80%9A%E8%BF%87%E2%80%9C%E5%BE%AE%E4%BF%A1%E5%9B%A2%E9%98%9F%E2%80%9D%E9%80%9A%E7%9F%A5%E4%BD%A0%E5%AE%A1%E6%A0%B8%E7%BB%93%E6%9E%9C%E3%80%82%E6%84%9F%E8%B0%A2%E4%BD%A0%E7%9A%84%E6%94%AF%E6%8C%81%E3%80%82")
    }
  },
  methods: {
    close() {
      this.$global.trackEvent( "落地页", "关闭投诉", "关闭跳转" ,"1", "guan");
      window.location = "http://www.baidu.com"
    }
  }
}
</script>

<style scoped>
.res_wrapper{
  background: #fff;
  text-align: center;
  padding: 80px 0 200px 0;
}
.iconfont{
  color: #20bf64;
  font-size: 130px;
}
.ok{
  font-size: 48px;
  color: #333;
  padding-top: 60px;
}
.p{
  padding: 40px 80px 100px 40px;
  font-size: 34px;
}
.btn{
  background: #20bf64;
  display: block;
  margin: 0 auto;
  color: #fff;
  width: 350px;
  height: 80px;
  line-height: 80px;
  border-radius: 10px;
  font-size: 34px;
}
</style>



