<template>
  <div>

  <div v-if="isResPage" class="list_wrapper">
    <div class="label">你可以:</div>
    <ul>
      <li @click="goDetail">
        <span>{{tip}}</span><em class="iconfont">&#xe76b;</em>
      </li>
    </ul>
  </div>

  <transition  name="fade-transform" mode="out-in">
    <res v-if="isResPage == false"/>
  </transition>

  </div>

</template>

<script>
import res from '@/components/res'
export default {
  name: 'tousuList',
  components: {
    res
  },
  data () {
    return {
      isResPage: true,
      tip: decodeURIComponent("%E6%8F%90%E4%BA%A4%E7%BB%99%E5%BE%AE%E4%BF%A1%E5%AE%A1%E6%A0%B8%E5%9B%A2%E9%98%9F")
    }
  },
  methods: {
    goDetail(){
      this.$global.trackEvent( "落地页", "投诉", "提交审核" ,"1");
      this.isResPage = false
    }    
  }  
}
</script>

<style scoped>
.list_wrapper .label{
  padding: 30px 30px 5px 30px;
  font-size: 30px;
  color: #666;
}
.list_wrapper ul{
  background: #fff;
  overflow: hidden;
}
.list_wrapper li{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
  margin-top: -1px;
  color: #000;
  font-size: 36px;
  border-top: 1px solid #e6e6e6
}
.list_wrapper .iconfont{
  font-size: 46px;
  color: #999
}

</style>



